.doctor {
    margin-top: 16px;

    &__info {
        display: flex;
        justify-content: flex-start;
        margin-bottom: 16px;
    }

    &__img {
        width: 44px;
        height: 44px;

        img {
            max-width: 44px;
            max-height: 44px;
            object-fit: cover;
            border-radius: 50%;

        }
    }

    &__title {
        margin-left: 16px;
        font-family: 'AktivGroteskCorp-Regular';
        font-weight: 400;
        font-size: 16px;
        line-height: 136%;
        color: #1E1E1E;
    }

    &__subtitle {
        color: #66D112;
    }

    &__link {
        box-sizing: border-box;
        padding-left: 48px;
        font-family: 'AktivGroteskCorp-Regular';
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: #1E1E1E;
        position: relative;

        &::after {
            width: 78%;
            right: 0;
            bottom: 0;
            content: "";
            position: absolute;
            height: 1px;
            background-color: #1E1E1E;
        }

        &::before {
            content: "";
            position: absolute;
            left: 0;
            width: 32px;
            height: 32px;
            z-index: 100;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url('/assets/images/icons/doctor-link-1.svg');
        }
    }
}

@media(min-width: 576px) {
    .doctor {
        &__info {
            justify-content: center;
            align-items: center;
        }
        &__link{
            margin: 0 auto;
            max-width: 215px;
            display: block;
        }
    }
}

@media(min-width: 768px){
    .doctor{
        display: flex;
        align-items: center;
        &__info{
            max-width: 232px;
        }
        &__title{
            text-align: left;
        }
        &__link::after{
            bottom: -2px;
        }
    }
}

@media(min-width: 1440px){

}

@media(min-width: 1920px){
    .doctor{
        align-items: initial;
        &__link{
            margin: 0;
            margin-left: auto;
            &::after{
                bottom: 36px;
            }
        }
    }
}