.map {

    &__email {
        font-weight: 400;
        font-size: 16px;
        line-height: 136%;
        color: #66D112;
        font-family: 'AktivGroteskCorp-Regular';
    }

    &__info {
        padding: 43px 18px 0 18px;
        margin-bottom: 48px;
    }

    .work-time {
        margin-top: 16px;
    }

    .links {
        margin-bottom: 16px;
        max-width: 180px;

        &__item {
            font-weight: 500;
            font-size: 20px;
            line-height: 132%;
            font-family: 'AktivGroteskCorp-Medium';
        }
    }

    &__social-item+&__social-item {
        margin-left: 10px;
    }

    .instagram {
        width: 19px;
        height: 19px;

        svg {
            width: 19px;
            height: 19px;
        }
    }

    .vk {
        width: 24px;
        height: 14px;

        svg {
            width: 24px;
            height: 14px;
        }
    }

    .youtube {
        width: 18px;
        height: 14px;

        svg {
            width: 18px;
            height: 14px;
        }
    }

    .facebook {
        width: 12px;
        height: 23px;

        svg {
            width: 12px;
            height: 23px;
        }
    }

    .address,
    .work-time {
        font-weight: 400;
        font-size: 16px;
        line-height: 136%;
    }

    &__wrapper {
        max-width: 1920px;
        margin: 0 auto;
    }
}

@media (min-width: 360px) {}

@media (min-width: 576px) {}

@media (min-width: 768px) {
    .map {
        &__wrapper {
            position: relative;
        }

        &__info {
            top: 105px;
            left: 12%;
            width: 375px;
            height: 384px;
            box-sizing: border-box;
            background-color: #ffffff;
            position: absolute;
            border-radius: 15px;
            padding: 48px 32px;
            margin-bottom: 0;
        }

        &__iframe {
            iframe {
                height: 594px;
            }
        }
    }




}

@media (min-width: 992px) {}

@media (min-width: 1200px) {}

@media(min-width: 1440px) {}

@media(min-width: 1920px) {
    .map{
        &__info{
            width: 386px;
            left: 7%;
        }
    }
}