.slider {
    box-sizing: border-box;
    padding: 48px 0;
    background-color: #EDEDED;

    &__wrapper {
        .container {
            padding: 0 16px;
        }
    }

    &__title {
        text-align: center;
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 32px;
        line-height: 124%;
        color: #1E1E1E;
        margin-bottom: 27px;
    }

    &__subtitle {
        margin: 0 auto;
        display: block;
        font-weight: 400;
        color: #000000;
        font-family: 'AktivGroteskCorp-Regular';
        font-size: 16px;
        line-height: 136%;
        display: none;
    }

    &__subtitle.slider__subtitle-custom {
        display: block;
        font-weight: 400;
        color: #000000;
        font-family: 'AktivGroteskCorp-Regular';
        font-size: 16px;
        line-height: 136%;
    }
}

.slides {
    &__title {
        font-weight: 700;
        font-size: 20px;
        line-height: 120%;
        font-family: 'AktivGroteskCorp-Bold';
        margin-bottom: 16px;
    }

    &__img {
        margin: 0 auto;
        width: 280px;
        height: 180px;
        position: relative;
        img {
            max-width: 288px;
            max-height: 180px;
            border-radius: 10px;
            object-fit: cover;
            width: 100%;
        }
    }

    &__img.img-primary {
        margin-bottom: 8px;
    }

    &__item.slides__item-second {
        display: none;
    }

    &__item {
        background-color: #FFFFFF;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.04);
        border-radius: 20px;
        box-sizing: border-box;
        padding: 32px 24px;
    }

    &__img-custom {
        text-transform: uppercase;
        box-sizing: border-box;
        padding: 9px 24px;
        box-sizing: border-box;
        position: absolute;
        bottom: 0;
        font-weight: 700;
        font-size: 13px;
        line-height: 128%;
        border-radius: 16px 0px 0px 0px;
        font-family: 'AktivGroteskCorp-Bold';
    }

    &__img-custom.custom-first {
        max-width: 92px;
        min-width: 77px;
        text-align: center;
        right: 0;
        background-color: #FEC42F;
        border-radius: 16px 0px 0px 0px;
    }

    &__img-custom.custom-second {
        max-width: 92px;
        left: 0;
        background-color: #AFE764;
        border-radius: 0px 16px 0px 0px;
    }
}

@media(min-width: 576px) {
    .slides {
        &__title {
            text-align: center;
        }
    }
}

@media(min-width: 768px) {
    .slick-slide.slick-current.slick-active {
        div {
            text-align: center;
        }
    }

    .slides {
        &__item {
            margin: 0 auto;
            max-width: 648px;
        }

        &__imgs {
            display: flex;
            justify-content: flex-start;
        }

        &__img {
            margin: 0;
        }

        &__img.img-secondary {
            margin-left: 8px;
        }

        &__title {
            text-align: left;
        }
    }
}

@media(min-width: 1440px) {
    .slides {
        &__item {
            padding: 47px 32px 6px;
            margin: 0;
        }

        &__item.slides__item-second {
            margin-left: 30px;
        }

        &__item-block {
            justify-content: center;
            display: flex !important;
        }

        &__item.slides__item-second {
            display: block;
        }
    }
}

@media(min-width: 1920px) {
    .slider {
        padding: 48px 0 26px;

        &__inner {
            position: relative;
        }

        .slider-btn.btn-prev {
            position: absolute;
            left: 0;
            top: 50%;
        }

        .slider-btn.btn-next {
            position: absolute;
            right: 0;
            top: 50%;
        }

        &__subtitle.slider__subtitle-custom {
            display: none;
        }

        &__subtitle {
            display: block;
        }
    }

    .slides {
        &__img-custom.custom-first {
            min-width: 92px;
        }
    }

}