.advice {
    &__img {
        .container {
            padding: 0;
        }

        img {
            width: 100%;
        }
    }

    &__content {
        text-align: center;
        background-color: #AFE764;
        box-sizing: border-box;
        padding: 32px 16px;

        .container {
            padding: 0;
        }
    }

    &__title {
        font-family: 'AktivGroteskCorp-Medium';
        font-weight: 500;
        font-size: 20px;
        line-height: 132%;
        color: #000000;
        margin-bottom: 32px;
    }

    &__subtitle {
        font-family: 'AktivGroteskCorp-Regular';
        font-weight: 400;
        font-size: 16px;
        line-height: 136%;
        color: #000000;
        margin-bottom: 8px;
    }

    &__link {
        font-family: 'AktivGroteskCorp-Medium';
        display: block;
        font-weight: 500;
        font-size: 24px;
        line-height: 124%;
        color: #000000;
    }
    &__img-item{
        img{
            max-width: 704px;
            max-height: 312px;
        }
    }
}

.banner {
    box-sizing: border-box;
    padding: 32px 24px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.04);
    border-radius: 8px;
    border: 1px solid #E1ECC8;
    background-color: #ffffff;

    &__title {
        font-family: 'AktivGroteskCorp-Medium';
        font-weight: 500;
        font-size: 20px;
        line-height: 132%;
        color: #000000;
        margin-bottom: 16px;
    }

    .return-call {
        max-width: 203px !important;
    }
}

@media (min-width: 361px) {
    .user {
        justify-content: space-around;
    }

    .header {
        &__top {
            justify-content: space-around;
        }
    }
}

@media (min-width: 576px) {
    .header.header-mobile {
        display: none;
    }

    .header-laptop {
        margin-bottom: 82px;
        padding-top: 8px;
        display: block;

        &__wrapper {
            display: flex;
            flex-wrap: wrap;
            align-items: center;

            button {
                margin-left: 20px;
            }

            div+div {
                margin-left: 20px;
            }
        }

        .address,
        .work-time {
            margin-bottom: 0;
        }

        .logo {
            margin-bottom: 12px;
        }


    }

    .advice {
        .container {
            background-color: #AFE764;
        }

        &__img-item {
            overflow: hidden;
            max-width: 554px;

            img {
                width: 100%;
                height: auto;
            }
        }

        &__img-item {
            position: relative;

            &::after {
                position: absolute;
                content: "";
                width: 30%;
                right: 0;
                top: 0;
                bottom: 0;
                height: 100%;
                background: linear-gradient(270deg, #AFE764 0%, rgba(175, 231, 100, 0) 100%), linear-gradient(270deg, #AFE764 0%, rgba(175, 231, 100, 0) 100%);
            }
        }
    }
}

@media (min-width: 768px) {}

@media (min-width: 992px) {}

@media (min-width: 1200px) {
    .advice {
        .container {
            height: 100%;
        }

        &__wrapper {
            display: flex;
        }

        &__img {
            width: 36%;

            img {
                width: auto;
            }
        }

        &__content {
            width: 64%;
        }

        &__content-wrapper {
            display: flex;
            align-items: center;
        }

        &__info {
            max-width: 500px;
            width: 100%;
        }
    }
}

@media(min-width: 1440px) {
    .banner {
        max-width: 512px;
    }
}

@media(min-width: 1920px) {
    .advice {
        &__title {
            max-width: 477px;
            font-size: 24px;
            line-height: 124%;
        }

        &__content {
            padding: 30px 16px;
        }

        &__content-wrapper {
            margin-top: 22px;
        }

        &__img-item {
            width: 100%;
            height: 312px;

            img {
                height: 312px;
            }
        }
    }

    .banner {
        margin-left: 34px;

        &__title {
            font-size: 24px;
            line-height: 124%;
        }
    }

    .banner .return-call {
        margin-top: 11px;
    }
}