.useful {
    height: 100%;
    padding-top: 52px;
    background: linear-gradient(97.07deg, #CFE8ED 0.4%, #DCF8A2 100.79%);

    &__title {
        text-align: center;
        color: #000000;
        font-weight: 700;
        font-size: 32px;
        line-height: 120%;
        font-family: 'AktivGroteskCorp-Bold';
        margin-bottom: 17px;
    }

    &__img {
        img {
            margin: 0 auto;
            display: block;
            max-width: 327px;
            max-height: 345px;
            object-fit: cover;
        }
    }

    &__item-img {
        img {
            max-width: 67px;
            max-height: 67px;
        }

        max-width: 67px;
        width: 100%;

    }

    &__list {
        margin-bottom: 32px;
    }

    &__item {
        margin: 0 auto;
        max-width: 328px;
        width: 100%;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        padding: 0 0 0 0;
        border: 2px solid #FFFFFF;
        border-radius: 50px;
    }

    &__item:nth-child(4) {
        .useful__item-img {
            margin-left: auto;
        }
    }

    &__item:nth-child(6) {
        .useful__item-img {
            margin-left: auto;
        }
    }

    &__item+&__item {
        margin-top: 16px;
    }

    &__item:nth-child(odd) {
        padding-right: 32px;

        .useful__item-content {
            margin-left: 16px;
        }
    }

    &__item:nth-child(even) {
        padding-left: 47px;

        .useful__item-content {
            margin-right: 16px;
        }
    }
}

@media (min-width: 360px) {}

@media (min-width: 576px) {
    .useful {
        &__title {
            margin-bottom: 32px;
        }

        &__item {
            max-width: 100%;
            max-width: 550px;
            justify-content: space-between;
        }
    }
}

