.prostheses {
    padding: 70px 0;
    background-color: #F5F5F5;

    &__title {
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 32px;
        line-height: 124%;
        color: #000000;
        text-align: center;
        margin-bottom: 32px;
    }

    &__item + &__item{
        margin-top: 32px;
    }

    &__img {
        max-width: 328px;
        max-height: 180px;
        overflow: hidden;
        border-radius: 10px;
        margin-bottom: 16px;
        img{
            width: 100%;
            object-fit: cover;
        }
    }

    &__content-title {
        position: relative;
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 20px;
        line-height: 26px;
        margin-bottom: 8px;
    }

    &__content-custom {
        box-sizing: border-box;
        padding: 5px;
        text-align: center;
        border-radius: 15px;
        position: absolute;
        right: 0;
        top: 0;
        font-weight: 700;
        font-size: 17px;
        line-height: 136%;
        color: #1E1E1E;
        width: 32px;
        height: 32px;
    }
}

@media (min-width: 360px) {
    .prostheses{
        &__item{
            max-width: 328px;
            margin: 0 auto;
        }
    }
}

@media (min-width: 576px) {
    .prostheses{
        &__list{
            justify-content: center;
        }
        &__item{
            margin: 10px;
        }
    }
}

@media (min-width: 666px) {
    .prostheses{
        &__item + &__item{
            margin-top: 0;
        }
    }
}

