.gift {
  max-width: 239px;
  margin: 0 auto;
  margin-bottom: 32px;
}
.gift__title {
  font-family: 'AktivGroteskCorp-Regular';
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 136%;
  color: #000000;
}
.gift__subtitle {
  font-weight: 700;
}
.gift-custom {
  height: 120px;
  width: 120px;
  margin: 0 auto 37px;
  background-color: #FF4638;
  position: relative;
}
.gift-custom__first,
.gift-custom__second {
  position: absolute;
  height: 120px;
  width: 120px;
  background-color: inherit;
  top: 0;
  left: -2;
}
.gift-custom__first {
  transform: rotate(15deg);
}
.gift-custom__second {
  transform: rotate(73deg);
}
.gift-custom::before,
.gift-custom::after {
  content: "";
  height: 120px;
  width: 120px;
  background-color: inherit;
  position: absolute;
  top: 0;
  left: 0;
}
.gift-custom::before {
  transform: rotate(37deg);
}
.gift-custom::after {
  transform: rotate(56deg);
}
.gift-custom__wrapper {
  top: 15px;
  position: absolute;
  width: 120px;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 100;
}
.gift-custom__icon {
  text-align: center;
  margin-bottom: 8px;
}
.gift-custom__content {
  color: #ffffff;
  font-weight: 500;
  font-size: 16px;
  line-height: 128%;
  text-align: center;
}
