.video {
    box-sizing: border-box;
    padding: 114px 0 10px 0;
    background-image: url('/assets/images/video-360.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 95vh;
    min-height: 512px;
    max-height: 502px;
    &__wrapper {
        text-align: center;
        .video__btn.return-call {
            max-width: 212px;
            min-width: 212px;
        }
    }

    &__title {
        margin-bottom: 40px;
        font-family: 'AktivGroteskCorp-Medium';
        font-weight: 500;
        font-size: 24px;
        line-height: 124%;
        color: #FFFFFF;
    }

    &__title.video__title-custom {
        display: none;
        font-family: 'AktivGroteskCorp-Medium';
        font-weight: 500;
        font-size: 24px;
        line-height: 124%;
        color: #FFFFFF;
        margin-bottom: 32px;
    }

    &__btn {
        background: linear-gradient(180deg, #B7ED44 39.58%, #8DDF23 100%);
        border: 4px solid rgba(183, 237, 68, 0.5);
        border-radius: 4px;
        color: #000000;
    }

    &__btn.return-call {
        max-width: 192px;
        min-width: 192px;
        text-decoration: none;
        padding: 14px 36px;
        &:hover {
            color: #ffffff;
        }
    }

    &__gift.gift-custom {
        width: 100px;
        height: 100px;
        margin: 0 auto 48px;
        position: relative;
        background-color: #FEC42F;

        &::after,
        &::before {
            width: 100px;
            height: 100px;
        }

        &:hover {
            svg {
                circle {
                    fill: #B7ED44;
                }
            }
        }

        svg {
            width: 44px;
            height: 44px;
            position: absolute;
            z-index: 100;
            top: 50%;
            left: 0;
            right: 0;
            margin: 0 auto;
            transform: translateY(-50%);
            transition: all .3s;

            circle {
                transition: all .3s;
            }
        }

    }

    .gift-custom__first,
    .gift-custom__second {
        width: 100px;
        height: 100px;
    }
}

@media (min-width: 360px) {

}

@media (min-width: 576px) {
    .video {
        max-height: initial;
        background-image: url('/assets/images/video-576.jpg');
    }
}

@media (min-width: 768px) {
    .video {
        background-image: url('/assets/images/video-768.jpg');
    }
}

@media (min-width: 992px) {
    .video {
        background-image: url('/assets/images/video-992.jpg');
    }
}

@media(min-width: 1200px) {
    .video {
        background-image: url('/assets/images/video-1200.jpg');
    }
}

@media(min-width: 1440px) {
    .video {
        background-image: url('/assets/images/video-1440.jpg');
    }
}

@media(min-width: 1920px) {
    .video {
        padding: 208px 0 10px;
        min-height: 640px;
        max-height: 640px;
        background-image: url('/assets/images/video-1920.jpg');

        &__title {
            font-size: 32px;
            line-height: 120%;
            display: none;
        }

        &__title.video__title-custom {
            display: block;
            font-size: 32px;
            line-height: 120%;
        }
    }
}