.header {
    padding: 12px 0 42px 0;
    background-color: #E8F2F4;

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 32px;
    }

    &__bottom {
        position: relative;
    }
}

.header-laptop{
    display: none;
}

.user {
    display: flex;
    justify-content: space-between;
}

@media (min-width: 361px) {
    .user {
        justify-content: space-around;
    }

    .header {
        &__top {
            justify-content: space-around;
        }
    }
}

@media (min-width: 576px) {
    .header.header-mobile{
        display: none;
    }
    .header-laptop{
        margin-bottom: 82px;
        padding-top: 8px;
        display: block;
        &__wrapper{
            display: flex;
            flex-wrap: wrap;
            align-items: center;

            button{
                margin-left: 20px;
            }
            div + div{
                margin-left: 20px;
            }
        }
        .address, .work-time{
            margin-bottom: 0;
        }
        .logo{
            margin-bottom :12px;
        }
    }
}

@media (min-width: 768px) {}

@media (min-width: 992px) {
    .header-laptop__wrapper{
        justify-content: center;
    }
}

@media (min-width: 1200px) {
    .header-laptop{

        &__wrapper{
            div, button{
                margin-left: 0;
            }
        }
        .address, .work-time{
            margin-bottom: 0;
        }
        .logo{
            margin-bottom :0;
            margin-right: 93px;
        }
        .work-time, .links{
            margin-left: 36px;
        }
        .social{
            margin-left: 73px;
        }
        .return-call{
            margin-left: auto;
            min-width: 203px;
            max-width: 203px;
        }
    }
}

@media(min-width: 1440px) {}

