.benefits {
    background-color: #F5F5F5;
    padding: 48px 0 34px;

    &__title {
        text-align: center;
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 32px;
        line-height: 124%;
        color: #000000;
        margin-bottom: 32px;
    }

    &__item {
        box-sizing: border-box;
        padding: 32px 24px;
        background: #FFFFFF;
        box-shadow: 0px 40px 60px rgba(0, 0, 0, 0.07);
        border-radius: 12px;
    }

    &__item+&__item {
        margin-top: 43px;
    }

    &__list-item+&__list-item {
        margin-top: 4px;
    }

    &__list-item {
        position: relative;
        box-sizing: border-box;
        padding-left: 37px;
        font-weight: 400;
        font-family: 'AktivGroteskCorp-Regular';
        font-size: 16px;
        line-height: 136%;
    }

    &__list-custom{
        color: #474747;
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
    }

    &__list-primary {
        &::before {
            width: 24px;
            height: 24px;
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            background-image: url('/assets/images/icons/benefits-primary-icon.svg');
            background-repeat: no-repeat;
            background-size: cover;
        }
    }

    &__list-secondary {
        &::before {
            content: "";
            position: absolute;
            width: 24px;
            height: 24px;
            left: 0;
            top: 0;
            background-image: url('/assets/images/icons/benefits-secondary-icon.svg');
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}

.sale {
    box-sizing: border-box;
    padding: 24px;
    border-radius: 10px;
    text-align: center;
    margin-bottom: 16px;

    &__title {
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 20px;
        line-height: 120%;
        margin-bottom: 16px;
    }

    &__new {
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 24px;
        line-height: 124%;
        color: #FF4638;
    }

    &__old {
        font-weight: 500;
        font-size: 18px;
        line-height: 124%;
        color: #474747;
        position: relative;

        &::before {
            position: absolute;
            content: "";
            width: 93px;
            height: 2px;
            top: 56%;
            background-color: #474747;
        }
    }
}

.sale.sale-primary {
    background-color: #FFD057;
}

.sale.sale-secondary {
    background-color: #AFE764;
}

