@media (min-width: 1200px) {
    .benefits{
        &__items{
            display: flex;
        }
        &__item{
            max-width: 784px;
            width: 100%;
        }
        &__item + &__item{
            margin-top: 0;
        }
        &__item:nth-child(2){
            margin-left: 32px;
        }
    }
    .sale{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

