.questions {
    background-color: #F5F5F5;

    .container {
        box-sizing: border-box;
        background-color: #EEF6DA;
        border-radius: 20px;
    }

    &__title {
        text-align: center;
        margin-bottom: 57px;
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 32px;
        line-height: 124%;
    }

    &__inner {
        padding: 52px 0;
    }
}

.items {
    margin-bottom: 32px;

    &__el {
        cursor: pointer;
        display: flex;
        box-sizing: border-box;
        align-items: center;
        padding: 7px 24px;
        background-color: #FFFFFF;
        border-radius: 4px;
        transition: all .3s;

        &:hover {
            background-color: #AFE764;

            .items__el-icon {
                svg {
                    path {
                        fill: #ffffff;
                    }
                }
            }
        }
    }

    &__el+&__el {
        margin-top: 8px;
    }

    &__el-content {
        margin-left: 16px;
    }
}

.items__el-icon,
.items__el-content {
    pointer-events: none;
}

.video__item-iframe {
    display: none;
}

.video__item-link {
    pointer-events:initial;
}

.video__list{
    pointer-events: none;
}

.video-item {
    flex-grow: 3;

    &__bg {
        min-height: 254px;
        min-width: 100%;

        .video__item-iframe {
            border-radius: 12px;
            min-height: 250px;
        }

        img {
            width: 100%;
            object-fit: cover;
        }

        max-width: 328px;
        max-height: 254px;
        position: relative;
        margin: 0 auto;
        text-align: center;
    }

    &__play {
        left: 0;
        right: 0;
        margin: 0 auto;
        top: 50%;
        transform: translateY(-50%);
        position: absolute;
    }

    &__title {
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 20px;
        line-height: 120%;
        position: absolute;
        max-width: 280px;
        text-align: center;
        left: 0;
        right: 0;
        margin: 0 auto;
        bottom: -15px;
        color: #ffffff;
        transform: translateY(-50%);
    }
}

@media (min-width: 360px) {}

@media (min-width: 576px) {
    .video-item {
        &__bg {
            max-width: 100%;
            min-height: 350px;

            picture {
                img {
                    height: 348px;
                }
            }
        }

        &__bg .video__item-iframe {
            min-height: 350px;
        }
    }

}

@media (min-width: 768px) {
    .items {
        margin-right: 10px;
    }

    .video-item {
        &__bg {
            min-height: 448px;
            picture {
                img {
                    height: 448px;
                }
            }
        }
        &__bg .video__item-iframe {
            min-height: 448px;
        }
    }
}

@media (min-width: 992px) {
    .questions {
        &__info {
            display: flex;
            justify-content: space-between;
        }
    }
    .items{
        width: 50%;
    }
    .video-item {
        width: 50%;
        &__title {
            max-width: 530px;
            bottom: 20px;
        }

        &__bg {
            max-height: initial;
            box-sizing: border-box;
            max-width: 752px;
            width: 100%;
            max-height: auto;

            .video__item-iframe {
                height: 448px;

            }

            iframe {
                max-width: 752px;
                width: 100%;
                height: 448px;
            }
        }
    }
}

@media(min-width: 1200px) {
    .video-item {
        &__title {
            max-width: 688px;
        }
    }

    .questions {
        &__info {
            justify-content: center;
        }

        .video-item {
            margin-left: 32px;
        }
    }
}

@media(min-width: 1440px) {
    .questions {
        &__title {
            text-align: left;
            margin-left: 32px;
        }

        .items {
            max-width: 752px;
            width: 100%;
        }
    }
}

@media(min-width: 1920px) {
    .video-item {
        transform: translateY(-19%);
    }

    .questions {
        &__inner {
            padding: 52px 0 0 0;
        }
    }
}