.discount {
    &__items{
        padding-bottom: 8px;
    }
    &__item {
        max-width: 328px;
        position: relative;
        z-index: 10;
        background: #FFFFFF;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.04);
        border-radius: 12px;
        box-sizing: border-box;
        padding: 24px 21px;
    }

    &__item+&__item {
        margin-top: 32px;
    }

    &__icon {
        margin-bottom: 16px;
        width: 48px;
        height: 48px;
        margin: 0 auto 16px;

        svg {
            width: 48px;
            height: 48px;
        }
    }

    &__title {
        font-family: 'AktivGroteskCorp-Bold';
        font-weight: 700;
        font-size: 20px;
        line-height: 120%;
        color: #000000;
        text-align: center;
        margin-bottom: 16px;
    }

    &__subtitle {
        font-family: 'AktivGroteskCorp-Bold';
        font-weight: 700;
        font-size: 17px;
        line-height: 136%;
        color: #000000;
        margin-bottom: 16px;
    }

    &__list {
        padding: 0;
    }

    &__list-item {
        box-sizing: border-box;
        font-family: 'AktivGroteskCorp-Regular';
        font-weight: 400;
        padding-left: 16px;
        font-size: 16px;
        line-height: 136%;
        color: #000000;
        list-style: none;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            width: 8px;
            height: 8px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background-color: #AFE764;
            border-radius: 4px;
        }
    }

    &__list-item+&__list-item {
        margin-bottom: 5px;
    }
}

.prices {
    box-sizing: border-box;
    padding-bottom: 15px;
    border-bottom: 1px solid #BCE08A;
    margin-bottom: 16px;

    &__new {
        font-family: 'AktivGroteskCorp-Bold';
        text-align: center;
        font-weight: 700;
        font-size: 24px;
        line-height: 124%;
        color: #FF4638;
        margin-bottom: 4px;
    }

    &__old {
        font-family: 'AktivGroteskCorp-Bold';
        text-align: center;
        font-weight: 500;
        font-size: 18px;
        line-height: 124%;
        color: #474747;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            left: 50%;
            top: 56%;
            transform: translateX(-50%) rotate(-1deg);
            width: 101px;
            height: 1px;
            background-color: #474747;
        }
    }
}

@media (min-width: 360px) {
    .discount {
        &__item {
            margin: 0 auto;
        }
    }

    .custom-img {
        margin: 0 auto;
    }
}

@media (min-width: 576px) {
    .discount {
        &__item {
            max-width: 512px;
        }
    }
}

@media (min-width: 768px) {}

@media (min-width: 992px) {}

@media (min-width: 1200px) {
    .discount {
        &__items {
            display: flex;
            justify-content: flex-start;
        }

        &__item {
            margin: 0;
        }

        &__item+&__item {
            margin-left: 40px;
        }
    }
}

@media(min-width: 1440px) {
    .discount {
        &__item {
            margin: 0;
        }

        &__item-top {
            border-bottom: 1px solid #bce08a;
            display: flex;
        }

        &__item:last-child {
            margin-left: 32px;
            margin-top: 108px;
            height: 224px;
        }

        &__item-primary {
            display: flex;
            width: 68%;
            margin-right: 16px;
        }

        &__icon {
            margin-left: 0;
            margin-right: 16px;
        }

        &__title {
            text-align: left;
        }

        .prices {
            padding-bottom: 7px;
            border-bottom: 0;
        }

        &__list-item.item-custom{
            display: none;
        }
    }
}

@media(min-width: 1920px) {
    .discount {
        &__item {
            padding: 24px 13px;
        }
        &__item-top{
            margin-bottom: 17px;
        }
        &__item:last-child {
            margin-top: 77px;
            height: 100%;
        }
    }
}