.specialists {
    background-color: #F5F5F5;
    padding: 64px 0 50px;

    &__title {
        text-align: center;
        margin-bottom: 32px;
        font-family: 'AktivGroteskCorp-Bold';
        font-weight: 700;
        font-size: 32px;
        line-height: 124%;
    }

    &__item {
        position: relative;
        background-color: #FFFFFF;
        max-width: 328px;
        width: 100%;
        box-sizing: border-box;
        padding-bottom: 48px;
        border-radius: 12px;
        overflow: hidden;
    }

    &__col+&__col {
        margin-top: 32px;
    }

    &__item.human-item {
        max-width: 376px !important;
        width: 100%;
    }

    &__item.content-item {
        max-width: 784px !important;
        width: 100%;
        box-sizing: border-box;
        padding: 23px 24px 32px;
        background-color: #EEF6DA;
        border-radius: 12px;
    }

    &__item-custom {
        box-sizing: border-box;
        padding: 20px 13px;
        text-align: center;
        position: absolute;
        width: 72px;
        height: 72px;
        font-weight: 500;
        border-radius: 50%;
        top: 60%;
        right: 16px;
        font-family: 'AktivGroteskCorp-Medium';
        color: #1E1E1E;
        font-size: 12px;
        line-height: 132%;
        background-color: #FEC42F;
    }

    &__item-custom.custom-primary {
        top: 59%;
    }

    &__item-custom.custom-secondary {
        top: 57%;
    }

    &__item-img {
        text-align: center;
        margin-bottom: 32px;
        img{
            max-width: 376px;
            max-height :320px;
            object-fit: cover;
        }
    }

    &__item-title {
        margin-left: 32px;
        max-width: 272px;
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 20px;
        line-height: 120%;
    }

    &__item-subtitle {
        margin-top: 16px;
        font-weight: 400;
        font-size: 12px;
        line-height: 132%;
        color: #474747;
        display: block;
    }

    &__item-advertisement {
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 32px;
        line-height: 124%;
        margin-bottom: 32px;
    }

}

.benefits-list {
    margin-bottom: 32px;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: 344px 344px;
    grid-template-columns: repeat(auto-fit, 290px);
    gap: 32px;

    &__item {
        box-sizing: border-box;
        position: relative;
        padding-left: 56px;
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 17px;
        line-height: 136%;

        &::before {
            position: absolute;
            content: "";
            background-image: url('/assets/images/icons/benefit-icon.svg');
            background-repeat: no-repeat;
            background-size: contain;
            width: 40px;
            height: 40px;
            left: 0;

        }
    }
}

.text {
    font-weight: 400;
    font-size: 16px;
    line-height: 136%;
    color: #1E1E1E;
}

@media (min-width: 360px) {
    .specialists {
        &__item {
            margin: 0 auto;
        }
    }

    .benefits-list {

        &__item {
            margin: 0 auto;
        }
    }
}

@media (min-width: 576px) {}

@media (min-width: 768px) {

    .specialists {
        &__items {
            justify-content: center;
        }

        &__item.human-item{
            height: 100%;
        }

        &__col {
            margin: 20px;
        }
        &__col + &__col{
            margin-top: 20px;
        }
    }

    .col-lg-4.specialists__col.human-col {
        padding: 0;
        max-width: 376px;
    }

    .col-md-6.specialists__col.human-col {
        padding: 0;
        max-width: 376px;
    }

    .col-sm-12.specialists__col.human-col {
        padding: 0;
        max-width: 376px;
    }

    .col-md-6.specialists__col.content-col {
        padding: 0;
        max-width: 784px;
        width: 100%;
    }
}

@media (min-width: 992px) {}

@media(max-width: 1200px){
    .col-md-6 {
        -ms-flex: 0 0 98%;
        flex: 0 0 98%;
    }
    .col-md-6.specialists__col.content-col {
        flex-grow: 3;
    }
}

@media(min-width: 1200px){
    .col-md-6.specialists__col.content-col {
        flex-grow: 3;
    }
}

@media(min-width: 1440px) {
}

@media(min-width: 1920px) {
    .specialists {
        &__title{
            text-align: left;
        }
        &__col {
            margin: 1px;
        }

        &__col+&__col {
            margin-left: 32px;
            margin-top: 0;
        }

        &__col.human-col {
            padding: 0;
            max-width: 25.333333%;
            max-width: 376px;
        }

        &__col.content-col {
            padding: 0;
            max-width: 376px;
            max-width: 49%;
            flex-grow: 4;
        }

        &__item.content-item {
            padding: 84px 18px 60px;
        }
    }

    .benefits-list {
        grid-template-columns: repeat(auto-fit, 341px);
    }
}