a{
    text-decoration: none !important;
    transition: all .3s;
    display: inline-block;
    color: inherit;
}

h1,
h2,
h3,
h4 {
    margin: 0;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

.text{
    p{
        margin-bottom: 0;
    }
}

button {
    padding: 0;
    cursor: pointer;
    box-sizing: border-box;
    background-color: transparent;
    border: none;
}

.container {
    margin: 0 auto;
    max-width: 1640px;
    padding: 0 20px;
}

.links {
    max-width: 150px;

    &__item {
        font-family: 'AktivGroteskCorp-Bold';
        font-weight: 700;
        font-size: 18px;
        line-height: 116%;
        color: #000000;
    }
}

.logo {
    max-width: 148px;

    img {
        width: 100%;
        object-fit: cover;
    }
}

.contacts-popup {
    display: block;
    box-sizing: border-box;
    padding: 20px;
    top: 56px;
    position: absolute;
    width: 204px;
    min-height: 208px;
    border-radius: 8px;
    background-color: #ffffff;
    left: -100%;
    transition: left 0.5s 0.5s;
    z-index: 1;
}

.contacts-popup-active {
    left: 0;
}

.address,
.work-time {
    font-weight: 400;
    font-family: 'AktivGroteskCorp-Regular';
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 12px;
    line-height: 132%;
    color: #000000;
}

.social {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .youtube {
        margin-left: 24px;
    }
}

.custom-img {
    max-width: 360px;
    width: 100%;
    height: 193px;

    img {
        object-fit: cover;
    }
}

@media (min-width: 360px) {}

@media (min-width: 380px) {
    .main {
        .custom-img {
            transform: translateX(10%);
        }
    }
}

@media (min-width: 430px) {
    .main {
        .custom-img {
            transform: translateX(20%);
        }
    }
}

@media (min-width: 500px) {
    .main {
        .custom-img {
            transform: translateX(45%);
        }
    }
}

@media (min-width: 576px) {
    .main {
        .custom-img {
            transform: translateX(25%);
        }
    }
}

@media (min-width: 670px) {
    .main {
        .custom-img {
            transform: translateX(45%);
        }
    }
}

@media (min-width: 768px) {
    .main {
        .custom-img {
            transform: translateX(25%);
        }
    }
}

@media (min-width: 890px) {
    .main {
        .custom-img {
            transform: translateX(45%);
        }
    }
}

@media (min-width: 992px) {
    .main {
        .custom-img {
            display: none;
            transform: translateX(15%);
        }
    }
}

@media(min-width: 1440px) {
    .main {
        .custom-img {
            display: none;
        }
    }
}

@media(min-width: 1920px) {}
