.main {
    background-color: #E8F2F4;
    overflow-x: hidden;
    &__title {
        font-family: 'AktivGroteskCorp-Bold';
        text-align: center;
        font-weight: 700;
        font-size: 32px;
        line-height: 120%;
        color: #000000;
        margin-bottom: 62px;
    }

    &__subtitle {
        color: #66D112;
    }
}

@media (min-width: 360px) {

}

@media (min-width: 576px) {
    .header {
        padding: 0 0 42px 0;
    }
}

@media (min-width: 768px) {}

@media (min-width: 992px) {
    .main {
        &__wrapper {
            background-image: url('/assets/images/main-1920.png');
            background-position: 569px 200px;
            background-size: 534px;
            background-repeat: no-repeat;
            min-height: 70vh;
            max-width: 100%;
        }
    }
}

@media (min-width: 1100px) {
    .main {
        &__wrapper {
            background-position: 680px 200px;
            background-size: 534px;
        }
    }
}

@media (min-width: 1200px) {
    .main {
        margin-bottom: 0;

        &__wrapper {
            background-position: 756px 135px;
            background-size: 616px;
            padding-bottom: 48px;
        }

        &__title {
            max-width: 470px;
            text-align: left;
        }

        .gift {
            margin-left: 0;
        }
    }
}

@media (min-width: 1370px) {
    .main {
        &__wrapper {
            background-position: 837px 135px;
        }
    }
}

@media(min-width: 1440px) {
    .main {

        &__wrapper {
            background-position: right;
            background-size: contain;
        }

        &__info {
            display: flex;
            justify-content: flex-start;
        }

        &__title {
            max-width: 648px;
            width: 100%;

        }

        &__title {
            margin-top: 30px;
            font-weight: 700;
            font-size: 48px;
            line-height: 116%;
            color: #000000;
        }

        .gift {
            margin: 0 0 32px 0;
        }
    }
}

@media(min-width: 1920px) {

    .main {

        .gift {
            transform: translateY(23px) translateX(-28px);
            max-width: 326px;
            margin-bottom: 0;
        }
    }
}