.gift {
    max-width: 239px;
    margin: 0 auto;
    margin-bottom: 32px;

    &__title {
        font-family: 'AktivGroteskCorp-Regular';
        text-align: center;
        font-weight: 400;
        font-size: 16px;
        line-height: 136%;
        color: #000000;
    }

    &__subtitle {
        font-weight: 700;
    }

    &-custom {
        height: 120px;
        width: 120px;
        margin: 0 auto 37px;
        background-color: #FF4638;
        position: relative;

        &__first,
        &__second {
            position: absolute;
            height: 120px;
            width: 120px;
            background-color: inherit;
            top: 0;
            left: -2;
        }

        &__first {
            transform: rotate(15deg);
        }

        &__second {
            transform: rotate(73deg);
        }

        &::before,
        &::after {
            content: "";
            height: 120px;
            width: 120px;
            background-color: inherit;
            position: absolute;
            top: 0;
            left: 0;
        }

        &::before {
            transform: rotate(37deg);
        }

        &::after {
            transform: rotate(56deg);
        }

        &__wrapper {
            top: 15px;
            position: absolute;
            width: 120px;
            left: 0;
            right: 0;
            margin: 0 auto;
            z-index: 100;
        }

        &__icon {
            text-align: center;
            margin-bottom: 8px;
        }

        &__content {
            color: #ffffff;
            font-weight: 500;
            font-size: 16px;
            line-height: 128%;
            text-align: center;
        }
    }

}