.contacts {
    padding: 10px 22px;
    font-weight: 700;
    font-size: 13px;
    line-height: 128%;
    color: #000000;
    text-transform: uppercase;
    background: linear-gradient(180deg, #B7ED44 39.58%, #8DDF23 100%);
    border: 4px solid rgba(183, 237, 68, 0.5);
    border-radius: 4px;
    transition: all .3s;

    &:hover {
        color: #ffffff;
    }
}

.return-call {
    font-weight: 700;
    font-family: 'AktivGroteskCorp-Bold';
    max-width: 174px !important;
    text-transform: uppercase;
    padding: 10px 17px;
    font-weight: 700;
    font-size: 13px;
    line-height: 128%;
    color: #FFFFFF;
    background: linear-gradient(180deg, #FF845E 0%, #FF3F31 82.29%);
    border: 4px solid rgba(255, 70, 56, 0.3);
    border-radius: 4px;
    transition: all .3s;

    &:hover {
        color: #b7ed44;
    }
}

.slider-btns{
    margin-top: 38px;
    text-align: center;
}

.slider-btn {
    width: 56px;
    height: 56px;
    background-color: #EEF7DA;
    border: 1px solid #AFE764;
    border-radius: 25px;
    position: relative;
    &:hover{
        svg{
            path{
                stroke: #000000;
            }
        }
    }
    &__icon {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
        margin: 0 auto;
        svg{
            path{
                transition: all .3s;
            }
        }
    }
    &.btn-prev{
        margin-right: 8px;
    }
    &.next-btn{
        margin-left: 8px;
    }
    &.btn-next {
        svg {
            transform: rotate(180deg);
        }
    }
}

@media(max-width: 1920px) {
    .return-call {
        max-width: 203px;
        width: 100%;
    }
}