.photos {
    padding: 48px 0;
    background-color: #EDEDED;

    &__wrapper {
        padding-left: 16px;
    }

    &__title {
        font-weight: 700;
        font-family: 'AktivGroteskCorp-Bold';
        font-size: 32px;
        line-height: 120%;
        color: #000000;
        margin-bottom: 32px;
    }

    &__slider-item {
        max-width: 268px;
        margin-right: 16px;
        overflow: hidden;

        img {
            width: auto;
            height: 365px;
        }
    }

}

@media (min-width: 360px) {

}

@media (min-width: 400px) {
    .photos {
        &__slider-item {
            max-width: 412px;
            width: 100%;
            img {
                width: auto;
                height: 500px;
            }
        }
    }
}

@media (min-width: 576px) {
    .photos {
        &__slider-item {
            max-width: 512px;
            width: 100%;

            img {
                width: auto;
                height: 550px;
            }
        }
    }
}

@media (min-width: 768px) {}

@media (min-width: 992px) {
    .photos {
        &__slider-item {
            max-width: 1000px;
            width: 100%;
            img {
                width: auto;
                height: 600px;
            }
        }
    }
}

@media(min-width: 1440px) {}

@media(min-width: 1920px) {
    .photos {
        &__wrapper{
            margin-top: 30px;
            margin-left: 120px;
        }
        &__slider-item {
            max-width: 1056px;
            width: 100%;
            img {
                width: auto;
                height: 696px;
            }
        }
    }
}